import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import './Toolbar.scss';
import Logo from './../Logo/Logo';
import PhoneBox from './../PhoneBox/PhoneBox';
import OrderBuilder from './../../../containers/OrderBuilder/OrderBuilder';
import Button from './../../UI/Button/Button';
import Weather from './../../Widgets/Weather/Weather';

const Toolbar = () => {

   

   return (
      <header className="Toolbar">
         <div className="Container">
            <div className="Toolbar-Box">
               <Logo/>
               <OrderBuilder/>
               <div className="Toolbar-Box__Price">
                  <NavLink to='/price'>
                     <Button btnStyle="Button__Green">ПРАЙС-ЛИСТ</Button>
                  </NavLink>
               </div>
               {/* <PhoneBox/> */}
               <div>
               <a href="https://clck.yandex.ru/redir/dtype=stred/pid=7/cid=1228/*https://yandex.md/weather/10313" target="_blank"><img src="https://info.weather.yandex.net/10313/2_white.ru.png?domain=ua" border="0" alt="Яндекс.Погода"/><img width="1" height="1" src="https://clck.yandex.ru/click/dtype=stred/pid=7/cid=1227/*https://img.yandex.ru/i/pix.gif" alt="" border="0"/></a>
               </div>
            </div>
         </div>
      </header>
   );
}

export default Toolbar;